import '../Detail.css';
import {useLocation,useParams } from "react-router-dom";
import data from "../data.json";

export default function Detail({c}){
    c('black');
    let {num} = useParams();



    return (
        <>

            <div className="detail_all">
                <div className="detail_tit">
                    <p>{data.work[num].tit}</p>
                </div>

                <div className="detail_link">
                    <p>사이트보러가기(<a href={data.work[num].web} target="_blank">{data.work[num].link}</a>)</p>
                </div>

                <div className="detail_text">
                    <p>{data.work[num].text_1}</p>
                    <p>{data.work[num].text_2}</p>
                    <p>{data.work[num].text_3}</p>
                </div>

                <div className="detail_skill">
                    <p>{data.work[num].skill}</p>
                </div>

                <figure>
                    {data.work[num].img.map((url,k)=>(
                        <img key={k} src={url} />
                    ))}
                </figure>
            </div>
        </>
    );
}