import main_bg from '../img/main_bg.png';
import main_bg_mobile_1 from '../img/main_bg_mobile_1.png';
import main_bg_mobile_2 from '../img/main_bg_mobile_2.png';
import main_bg_mobile_3 from '../img/main_bg_mobile_3.png';
import main_bg_mobile_4 from '../img/main_bg_mobile_4.png';
import main_bg_mobile_5 from '../img/main_bg_mobile_5.png';
import main_bg_mobile_6 from '../img/main_bg_mobile_6.png';
import main_bg_mobile_7 from '../img/main_bg_mobile_7.png';
import main_bg_2 from '../img/main_bg_2.png';
import main_bg_2_mobile_1 from '../img/main_bg_2_mobile_1.png';
import main_bg_2_mobile_2 from '../img/main_bg_2_mobile_2.png';
import main_bg_2_mobile_3 from '../img/main_bg_2_mobile_3.png';
import main_bg_2_mobile_4 from '../img/main_bg_2_mobile_4.png';
import main_bg_2_mobile_5 from '../img/main_bg_2_mobile_5.png';
import main_bg_2_mobile_6 from '../img/main_bg_2_mobile_6.png';
import main_bg_2_mobile_7 from '../img/main_bg_2_mobile_7.png';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import {Link,useLocation } from "react-router-dom";
import {useState} from 'react';

import '../App.css';

export default function Work({c}){
    c('white');
    
    

    let [workNum, setWorkNum] = useState(0);

    const settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        beforeChange: function(current, next){
            setWorkNum(next);
            let num = document.querySelector('.num > span:nth-child(1)');
            num.innerHTML = `0${next+1}`;
            // 숫자

            let tit = document.querySelector('.tit > a');            
            if(next == 0){
                tit.textContent = `YSL Beauty Korea`
            }if(next == 1){
                tit.textContent = `Canon Eos Rchive`
            }
            // 사이트명
        }
    }

    return(
        <>
            <Slider {...settings}> 
                    <div class="main_bg">
                        <img src={main_bg} alt="main_bg"/>
                        <img src={main_bg_mobile_1} alt="main_bg_mobile_1"/>
                        <img src={main_bg_mobile_2} alt="main_bg_mobile_2"/>
                        <img src={main_bg_mobile_3} alt="main_bg_mobile_3"/>
                        <img src={main_bg_mobile_4} alt="main_bg_mobile_4"/>
                        <img src={main_bg_mobile_5} alt="main_bg_mobile_5"/>
                        <img src={main_bg_mobile_6} alt="main_bg_mobile_6"/>
                        <img src={main_bg_mobile_7} alt="main_bg_mobile_7"/>
                    </div>
                    <div class="main_bg">
                        <img src={main_bg_2} alt="main_bg_2"/>
                        <img src={main_bg_2_mobile_1} alt="main_bg_2_mobile_1"/>
                        <img src={main_bg_2_mobile_2} alt="main_bg_2_mobile_2"/>
                        <img src={main_bg_2_mobile_3} alt="main_bg_2_mobile_3"/>
                        <img src={main_bg_2_mobile_4} alt="main_bg_2_mobile_4"/>
                        <img src={main_bg_2_mobile_5} alt="main_bg_2_mobile_5"/>
                        <img src={main_bg_2_mobile_6} alt="main_bg_2_mobile_6"/>
                        <img src={main_bg_2_mobile_7} alt="main_bg_2_mobile_7"/>
                    </div>
            </Slider>
            <div className="set">
                <div className="num">
                    <span>01</span>
                    <span>/</span>
                    <span>02</span>
                </div>

                <div className="tit">
                    <Link to={`/detail/${workNum}`}>YSL Beauty Korea</Link>
                </div>

                <div className="see">
                    <Link to={`/detail/${workNum}`}>작품 보러가기</Link>
                </div>
            </div>
        </>
    );
}