import {useState, useEffect} from "react";
import Profile from './menu/Profile';
import Work from './menu/Work';
import Detail from './menu/Detail';
import data from "./data.json";
import { BrowserRouter ,Route, Switch, Link } from "react-router-dom";
import './App.css';


function App() {
  

  
  function pathFun(n){
    console.log(n)    
    setTimeout(()=>{
      let all = document.getElementsByClassName('all')[0];  
      if(n == 'white'){
        all.classList.remove('black')
        all.classList.add('white');
      }else{
        all.classList.remove('white');
        all.classList.add('black')
      }    
    },10)
    
  }
  

  function contect(){
    const cTitle = document.getElementsByClassName('ctitle')[0];
    const cList = document.getElementsByClassName('clist')[0];
          cList.classList.toggle('active'); 
  }

  function portfolio_list(){
    const pTitle = document.getElementsByClassName('ptitle')[0];
    const pList = document.getElementsByClassName('plist')[0];
          pList.classList.toggle('active'); 
  }
            
            

  return (
    <div class="all white">
      <BrowserRouter basename="/zez/">
        <header >
          <Link to="/" ><span>EunJung</span><span>Portfolio.</span></Link>
          <div class="header_right">
            <Link to="/Profile" >Profile</Link>
            <Link to="/" >Work</Link>
          </div>
        </header>
        <Switch> 
          <Route exact path="/"> <Work c={pathFun}/> </Route>
          <Route path="/Profile"> <Profile c={pathFun}/> </Route>
          <Route path="/detail/:num"> <Detail c={pathFun}/> </Route>
        </Switch>


        <footer>
          <div className="portfolio_list">
            <div className="ptitle" onClick={portfolio_list}>Portfolio List</div>
            <div className="plist">
                
                {data.work.map((w,k)=>(
                  <p key={k}> 
                    <Link to={`/detail/${k}`}>
                      0{k+1}. {w.tit}
                    </Link>
                  </p>
                ))}
                
                {/* json에 추가하면 리스트에도 출력됨 */}
            </div>
          </div>
          

          <div className="contect_list">
          <div className="ctitle" onClick={contect}>Contect</div>
            <div className="clist">
                <p>
                  email. eee09eee@gmail.com
                </p>
                <p>
                  Tel. 010 3318 6818
                </p>
            </div>
          </div>

        </footer>
        </BrowserRouter>
      </div>
      
  );
}

export default App;
